import { Component, ElementRef, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { AppService } from '../services/app.service';




@Component({
  selector: 'app-forget-password',
  templateUrl: './forget-password.page.html',
  styleUrls: ['./forget-password.page.scss'],
})
export class ForgetPasswordPage implements OnInit {

  message_sent:boolean
  errorMessage = ''

  constructor(private _AppService:AppService, private _ElementRef:ElementRef, private _Router:Router, private _NavController:NavController) { }


  forget_password(form){
    this._AppService.post_data(`resetPassword`,{
      "email":form.value.email
    },'s').subscribe((res:any)=>{
      console.log(res)
      if(res.message == "'Great! Successfully send forget password mail'"){
        this.errorMessage = ''
        this.message_sent = true
        this._ElementRef.nativeElement.querySelector('.input').value = ''
        setTimeout(() => {
          this._Router.navigate(['intro'])
        }, 7000);
      }
    },error =>{
      console.log(error)
      if(error.error.message == 'this email is not register '){
        this.errorMessage = 'هذا البريد الإلكتروني غير مسجل من قبل.'
      }
    })
  }

  back(){
    this._NavController.pop()
  }



  ngOnInit() {
    this.message_sent = false
  }

}
